import React, {Component} from 'react';
import { NavLink } from 'react-router-dom';
import './Home.css';
import clouds from './Clouds.svg'
import ironManBackground from './images/iron-man/background.png'
import ironManBuildings from './images/iron-man/buildings.png'
import ironMan from './images/iron-man/iron-man.png'
import planeBackground from './images/plane/background.png'
import planeGround from './images/plane/ground.png'
import plane from './images/plane/plane.png'
import ScrollMagic from './ScrollMagic';
import Headline from './Headline';

class Home extends Component {

  constructor(props) {
        super(props);
        this.controller = new ScrollMagic.Controller();
    }

  componentDidMount() {
        // build scenes
        new ScrollMagic.Scene({triggerElement: "#plane-group", duration: 1000, offset: 50})
            .setTween("#plane-group", 1, {transform: 'translate(-100%, -50%)'})
            .addTo(this.controller);
    }

  render(){
    return(
        <div>
          <div className="parallax">

            <Headline
              text="Welcome"
              backgroundColor="bg-light-red"
            />

            <div id="group2" className="parallax__group">
              <div className="parallax__layer parallax__layer--back">
                <img className="img" src="http://yodobi.com/4k-Wallpapers/4k-wallpapers-phone-Is-4K-Wallpaper.jpg" alt="bg1" />
              </div>
              <div className="parallax__layer parallax__layer--closer">
                <img className="img clouds" src={clouds} alt="clouds" />
              </div>
            </div>

            <Headline
              text="This is my Website"
              backgroundColor="bg-light-blue"
            />

            <div id="group3" className="parallax__group">
              <div className="parallax__layer parallax__layer--deep">
                <img className="img" src={ironManBackground} alt="bg1" />
              </div>
              <div className="parallax__layer parallax__layer--back">
                <img className="img" src={ironManBuildings} alt="bg1" />
              </div>
              <div className="parallax__layer parallax__layer--fore">
                <img className="img" src={ironMan} alt="ironMan" />
              </div>
            </div>

            <Headline
              text="This is my Website"
              backgroundColor="bg-light-blue"
            />

            <div id="group4" className="parallax__group">
              <div className="parallax__layer parallax__layer--back">
                <img className="img" src={planeBackground} alt="bg1" />
              </div>
              <div className="parallax__layer parallax__layer--closer">
                <img className="img" src={planeGround} alt="bg1" />
              </div>
              <div className="parallax__layer parallax__layer--base">
                <div id="plane-group" className="img">
                  <img id="plane" src={plane} alt="plane" />
                  <div id="trail"></div>
                </div>
              </div>
            </div>

            <NavLink to='/randoms'>
              <Headline
                text="random stuff"
                backgroundColor="bg-orange"
              />
            </NavLink>

          </div>
        </div>

    );
  }
}

export default Home;
